<template>
  <div class="fonrt-home">
      <div class="fonrt-card" v-for="k in 4" :key="k">
          <a>
            <a-card>
              <span slot="title">
                  <a class="fonrt-title">GIT</a>
              </span>
              <p class="fonrt-content">
                Git 是一个开源的分布式版本控制系统，用于敏捷高效地处理任何或小或大的项目。
                Git 是 Linus Torvalds 为了帮助管理 Linux 内核开发而开发的一个开放源码的版本控制软件。
                Git 与常用的版本控制工具 CVS, Subversion 等不同，它采用了分布式版本库的方式，不必服务器端软件支持。
              </p>
            </a-card>
          </a>
      </div>
      <div>
        <template>
          <a-pagination 
            :default-current="1" 
            :total="data.total"
            :current="data.page"
            :defaultPageSize="data.size" />
        </template>
      </div>
  </div>
</template>

<script>
export default {
    name: "FonrtHome",
    data () {
      return{
        data: {
          page: 1,
          total: 10,
          size: 4
        }
      }
    }
}
</script>

<style scoped>
    .fonrt-home{
      width: 80%;
      padding: 2% 0;
      margin-left: 18%;
    }
    .fonrt-card{
      margin: 5% 0;
    }
    .fonrt-content{
      text-indent: 2em;
      line-height: 2em;
    }
    .ant-pagination{
      float: right;
    }
</style>>


